body {
  text-transform: lowercase;
  color: #696969;
  font-size: 1.6em;
  font-family: monospace;
  /* background-color: rgb(255, 255, 189); */
  background-color: white;
  overflow-wrap: break-word;
  padding: 2px;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-direction: column;
      flex-direction: column;
  text-shadow: 3px 3px 1px rgba(0, 0, 0, 0.15);
}

.container {
  margin-top: 20px;
  display: grid;
}

.content {
  text-shadow: none;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: -webkit-gradient(
    linear,
    left top, right bottom,
    from(black),
    color-stop(rgba(0, 0, 0, 0.75)),
    color-stop(rgba(0, 0, 0, 0.6)),
    to(rgba(0, 0, 0, 0.2))
  );
  background-image: -webkit-linear-gradient(
    top left,
    black,
    rgba(0, 0, 0, 0.75),
    rgba(0, 0, 0, 0.6),
    rgba(0, 0, 0, 0.2)
  );
  background-image: -o-linear-gradient(
    top left,
    black,
    rgba(0, 0, 0, 0.75),
    rgba(0, 0, 0, 0.6),
    rgba(0, 0, 0, 0.2)
  );
  background-image: linear-gradient(
    to bottom right,
    black,
    rgba(0, 0, 0, 0.75),
    rgba(0, 0, 0, 0.6),
    rgba(0, 0, 0, 0.2)
  );
  padding-top: 25px;
}

.links,
footer {
  margin-top: 35px;
}

footer span {
  display: block;
  font-size: 0.5em;
  opacity: 0.6;
  letter-spacing: 1px;
  margin: 5px;
}

footer div {
  margin: 10px;
  background-color: white;
}

input {
  width: 40px;
  text-align: center;
  opacity: 0.3;
  border-radius: 5px;
}

.im {
  padding: 10px;
}
.im:hover {
  opacity: 0.5;
}
.show-more {
  /*text-align: right;*/
  -webkit-text-fill-color: black;
  margin: -20px 0 20px 0;
  -webkit-transition-duration: .5s;
       -o-transition-duration: .5s;
          transition-duration: .5s;
}

svg {
  -webkit-transition-duration: .25s;
       -o-transition-duration: .25s;
          transition-duration: .25s;

}

.show-more:hover, svg:hover {
  opacity: .7;
}

.bg-text,
.im {
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.left {
  text-align: left;
}

.bg-inner {
  background-color: white;
  padding: 10px;
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  display: inline-block;
}

.bg-bottom {
  padding: 0 0 5px 0;
}

h1 {
  font-size: 3.5em;
  font-weight: 1000;
  margin: -20px 0 0;
}

h2,
h3,
h4 {
  margin: 0;
}

.social-icons {
  justify-self: left;
  bottom: 0;
  position: fixed;
  background-color: transparent;
}

.input,
.controls {
  position: fixed;
  top: 10px
}

.controls {
  justify-self: right;
}

.controls svg {
  padding: 5px
}

.input {
  left: 30px;
  top: 30px;
}

a {
  cursor: crosshair;
  -webkit-transition-duration: .25s;
       -o-transition-duration: .25s;
          transition-duration: .25s;
  text-decoration: none;
  color: black;
}

.regular-link, #temp:hover {
  opacity: .45;
}

a:hover, .bg:hover {
  -webkit-transition-duration: .25s;
       -o-transition-duration: .25s;
          transition-duration: .25s;
  -webkit-text-decoration: strike-through;
          text-decoration: strike-through;
  opacity: 1;
}

svg {
  /* filter: drop-shadow(0.5px 0.7px 0.5px rgba(0, 0, 0, 0.6)); */
  -webkit-filter: drop-shadow(0.75px 1px 1px rgba(0, 0, 0, 0.4));
          filter: drop-shadow(0.75px 1px 1px rgba(0, 0, 0, 0.4));
}

.links a {
  color: white;
  text-shadow: 0.25px 0.25px 0.5px black;
  display: inline-block;
  text-decoration: none;
}

#temp {
  -webkit-transition-duration: 4.5s;
       -o-transition-duration: 4.5s;
          transition-duration: 4.5s;
  font-size: 18px;
}

#temp:hover {
  font-size: 36px;
}

.bg {
  padding: 8px 20px;
  margin: 8px;
  border-radius: 10px;
  opacity: 1;
  opacity: 0.75;
  display: inline-block;
  /* -webkit-background-clip: none; */
  -webkit-text-fill-color: white;
  font-size: 1em;
  font-weight: 100;
  background-position-y: 80%;
}

.bg-left {
  background-position-x: 25%;
}

.bg-right {
  background-position-x: 75%;
}

.bg-1 {	
  background-position-x: 15%;
}

.bg-2 {	
  background-position-x: 35%;
}

.bg-3 {	
  background-position-x: 55%;
}

.bg-4 {
  background-position-x: 75%;
}




